@import url(https://fonts.googleapis.com/css?family=Dosis:400,500,600,700&display=swap);
@import url(https://fonts.googleapis.com/css?family=Chelsea+Market&display=swap&subset=latin-ext);

body {
  margin: 0;
  font-family: "Chelsea Market", "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

.container {
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  grid-template-rows: repeat(5, 1fr);
  grid-column-gap: 0px;
  grid-row-gap: 0px;
  height: 100vh; }

.header {
  grid-column: 1 / 6;
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  grid-template-rows: repeat(2, 1fr); }
  .header__location {
    grid-column: 2 / 4; }
  .header__date {
    grid-column: 2 / 4;
    grid-row: 2; }

.today-container {
  grid-column: 1 /6;
  grid-row: 2/5;
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  grid-template-rows: repeat(5, 1fr); }

.today {
  grid-column: 3/4;
  grid-row: 2/3;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: repeat(3, 1fr);
  grid-column-gap: 0px;
  grid-row-gap: 0px; }
  .today__title {
    grid-area: 1 / 2 / 2 / 3;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 2em;
    font-weight: bold; }
  .today__tempicon {
    grid-area: 2 / 1 / 3 / 4;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(2, 1fr);
    grid-column-gap: 0px;
    grid-row-gap: 0px;
    font-size: 2em; }
  .today__icon {
    grid-column: 1/2;
    display: flex;
    justify-content: center;
    align-items: center; }
  .today__temperature {
    grid-column: 2/3;
    display: flex;
    justify-content: center;
    align-items: center; }
  .today__status {
    grid-area: 3 / 2 / 4 / 3;
    display: flex;
    justify-content: center;
    align-items: center; }

footer {
  grid-area: 5 / 1 / 6 / 6;
  display: grid;
  grid-template-columns: repeat(5, 1fr); }

.forecast {
  grid-column: 2/5;
  display: flex;
  grid-template-columns: repeat(7, 1fr); }
  .forecast__item {
    display: flex;
    justify-content: center;
    align-items: center;
    display: flex;
    flex-direction: column; }

